    display: -ms-flexbox;
    display: flex;
}
.loadingPage .loading {
    width: 200px;
    height: 200px;
    position: relative;
}
.loadingPage .loading::before, .loadingPage .loading::after {
      content: '';
      width: 0;
      height: 0;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      margin: auto;
      position: absolute;
      background-color: #000;
      border-radius: 50%;
      -webkit-animation: loading 1.5s linear infinite;
              animation: loading 1.5s linear infinite;
}
.loadingPage .loading::after {
      -webkit-animation-delay: 0.75s;
              animation-delay: 0.75s;
}
@-webkit-keyframes loading {
0% {
    opacity: 1;
    width: 0;
    height: 0;
}
100% {
    opacity: 0;
    width: 50px;
    height: 50px;
}
}
@keyframes loading {
0% {
    opacity: 1;
    width: 0;
    height: 0;
}
100% {
    opacity: 0;
    width: 50px;
    height: 50px;
}
}
